.kore-chat-footer {
  background: none !important;
  border: none !important;
}

.kore-chat-footer .chatInputBox {
  border-radius: 50px !important;
  background: #ffffff !important;
  box-shadow: 0 8px 7px 0px hsl(0deg 0% 0% / 19%) !important;
  height: 50px !important;
  padding: 16px !important;
  border: 0px !important;
}

.kore-chat-header {
  background-image: url("https://s3-alpha-sig.figma.com/img/70d0/7dde/cb260554bdaa47c87adbdef01f9a1ac0?Expires=1702252800&Signature=BGuimQKb7YUXnos53aFpg3jMqJKsnSZcSbtc5L9aUD5ZLRETqlzj1A-ux1SCa5I6A9M6yYvVoSSeT1Z9c-frer3kQiHritJDBbw7nxMnNmMWflvPEJSEu7eTeV1AQFp2WJiTau1uvunV8XWK8Utng0WOqeyKLr6CURXsj3apzUO~CVMs0dZ1eYe8nB2uHx7pr9j6l0kFtcfslyiSD59Zzo98wpSTncycP8PhRWmUgQc776wbH1~lMqdDi~-0eHlw27fmh9hqBau1G0sDWTtpT6ixz~aDZpE20skw6gXeUlC~Crx-HH1cKDpDXKtsTZDmfGe~Fkb~dlkmpGVhvbtUqg__&Key-Pair-Id=APKAQ4GOSFWCVNEHN3O4") !important;
  background-size: cover !important;
  height: 72px !important;
  border-radius: 11px 11px 0 0 !important;
}

.kore-chat-window {
  border-radius: 11px !important;
  width: 400px !important;
  min-width: 400px !important;
  height: 580px !important;
  max-height: 90% !important;
  min-height: 200px !important;
  transition: 0.3s all ease-out !important;
}

#KoreMobileMaxWindow {
  height: 100% !important;
  max-height: 100% !important;
  min-height: 100% !important;
}
#KoreMaxWindow {
  width: 650px !important;
  max-width: 98% !important;
  height: 660px !important;
  max-height: 90% !important;
  min-height: 200px !important;
}

@media (max-width: 450px) {
  .kore-chat-window {
    width: 100% !important;
    min-width: 100% !important;
    height: 580px !important;
    max-height: 90% !important;
    min-height: 200px !important;
    right: 0 !important;
    bottom: 0 !important;
    top: auto !important;
    left: auto !important;
  }
}

@media (min-width: 450px) and (max-width: 767px) {
  .kore-chat-window {
    width: 400px !important;
    min-width: 400px !important;
    height: 580px !important;
    max-height: 90% !important;
    min-height: 200px !important;
    right: 10px !important;
    bottom: 10px !important;
    left: auto !important;
    top: auto !important;
  }
}

.kore-chat-window .kore-chat-body {
  top: 71px !important;
  bottom: 53px !important;
}

.Kore_header_Shadow {
  box-shadow: 0 10px 18px 0 rgb(0 0 0 / 30%) !important;
  z-index: 1 !important;
  width: 100% !important;
  height: 71px;
  display: block !important;
  position: absolute !important;
  left: 0px;
  border-radius: 11px 11px 0 0;
}

.kore-chat-window .sendBtnCnt .sendButton {
  border-radius: 50px !important;
  background-color: #004685 !important;
}

.kore-chat-header #headerTitle {
  width: 100% !important;
  font-size: 16px !important;
  font-weight: bold !important;
  text-overflow: ellipsis !important;
  overflow: hidden !important;
  white-space: nowrap !important;
  float: left !important;
}

.kore-chat-window.minimize .minimized {
  bottom: 0 !important;
}

.kore-chat-window .kore-chat-body ul.chat-container {
  padding: 20px 20px 0px 10px;
  height: 95% !important;
  top: 0 !important;
}

.reload-btn,
.minimize-btn,
.expand-btn,
.close-btn,
.kore-chat-window.minimize .minimized-title,
.kore-chat-window.minimize .minimized,
.kore-chat-window .endChatContainer,
.kore-chat-footer .chatSendMsg,
.kore-chat-window .historyLoadingDiv {
  display: none !important;
}

.kore-chat-window .ps__thumb-y {
  background-color: #004685 !important;
  width: 10px !important;
  border-radius: 0px !important;
}
.ps--focus > .ps__rail-x,
.ps--focus > .ps__rail-y,
.ps--scrolling-x > .ps__rail-x,
.ps--scrolling-y > .ps__rail-y,
.ps:hover > .ps__rail-x,
.ps:hover > .ps__rail-y {
  opacity: 9 !important;
}

/* CLOSED DIALOG BOX CSS */
.shadow-dialog-container {
  height: 100% !important;
  width: 100%;
  position: absolute;
  right: 0%;
  z-index: 1000;
  background-color: black;
  opacity: 0.4;
  bottom: 0px;
  border-radius: 11px;
}

.kore-chat-window.minimize {
  width: 0 !important;
  height: 0 !important;
  min-width: 0 !important;
  min-width: 0 !important;
}

.kore-chat-header #headerTitle strong {
  color: var(--White, #fff) !important;
  font-family: Circular Std !important;
  font-size: 19px !important;
  font-style: normal !important;
  font-weight: 900 !important;
  line-height: normal !important;
} 

.kore-chat-header #headerTitle span {
  color: var(--White, #FFF) !important;
  font-family: Circular Std !important;
  font-size: 14px !important;
  font-style: normal !important;
  font-weight: 450 !important;
  line-height: normal !important;
}

.koreHideLiMessage {
  display: none !important;
}