.kore-feedback-template-container {
    display: flex !important;
    flex-direction: row !important;
    justify-content: flex-end !important; 
    align-items: center !important;
}

.kore-feedback-text {
    color: gray !important;
    margin-right: 15px !important;
}

.kore-feedback-icon {
    border: 1px solid #e2e2e2 !important;
    border-radius: 50% !important;
    padding: 3px !important;
    margin-right: 8px !important;
    cursor: pointer !important;
    display: flex !important;
    flex-direction: row !important;
    justify-content: center !important;
    align-items: center !important;
}

.kore-feedback-icon.yesClicked {
    background-color: #198658 !important;
    border: 1px solid #198658 !important;
}
.kore-feedback-icon.noClicked {
    background-color: #d63d0a !important;
    border: 1px solid #d63d0a !important;
}

.kore-feedback-icon.yesClicked svg {
    color: white !important;
}

.kore-feedback-icon.noClicked svg {
    color: white !important;
}
