.jaida_custom_button_template_title {
  font-family: Circular Std;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  padding-top: 0px;
  cursor: pointer;
  }
  .jaida_custom_button_template_card:hover{
    color: white !important;
    background-color: #004685;
  }
  
  .jaida_custom_button_template_card {
    background-color: white;
    margin: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 320px;
    height: 48px;
    border-radius: 0px 16px 16px 0px;
    cursor: pointer;
    color: #004685;
  }
  
  .jaida_custom_button_template-kore-icon{
    background-color: #004685;
     width: 50px; 
     height: 48px;
    margin-top: 0px;
  }
  
  .messageIconJaida {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    fill: var(--White, #fff);
  }
  
  .messageIconJaidaText {
    margin-top: -35px;
    margin-left: 17px;
    color: var(--Black, #000);
    font-family: Circular Std;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  
  
  .koreAgentButtonIcon {
    font-size: 30px !important;
    color: gray !important;
    padding-top: 3.5px !important;
  }

  .jaida_custom_button_template_subText {
    font-size: 13px !important;
    background-color: white !important;
    border-radius: 8px !important;
    width: 88% !important;
    padding: 10px !important;
    font-weight: 500 !important;
    margin-top:-8px !important
  }

  .jaida_custom_button_template_text {
    font-size: 13px !important;
    background-color: white !important;
    border-radius: 8px !important;
    width: 88% !important;
    padding: 10px !important;
    font-weight: 500 !important;
  }
  .koreCustomForwordIcon {
    font-size: 30px !important;
    margin-top: 10px !important;
    color: #474141 !important;
  }